html,
body,
div,
span,
applet,
object,
iframe,
h1,
h2,
h3,
h4,
h5,
h6,
p,
blockquote,
pre,
a,
abbr,
acronym,
address,
big,
cite,
code,
del,
dfn,
em,
img,
ins,
kbd,
q,
s,
samp,
small,
strike,
strong,
sub,
sup,
tt,
var,
b,
u,
i,
center,
dl,
dt,
dd,
ol,
ul,
li,
fieldset,
form,
label,
legend,
table,
caption,
tbody,
tfoot,
thead,
tr,
th,
td,
article,
aside,
canvas,
details,
embed,
figure,
figcaption,
footer,
header,
hgroup,
menu,
nav,
output,
ruby,
section,
summary,
time,
mark,
audio,
video {
	margin: 0;
	padding: 0;
	border: 0;
	font-size: 100%;
	font: inherit;
	vertical-align: baseline;
}

/* HTML5 display-role reset for older browsers */
article,
aside,
details,
figcaption,
figure,
footer,
header,
hgroup,
menu,
nav,
section {
	display: block;
}

body {
	line-height: 1;
}

ol,
ul {
	list-style: none;
}

blockquote,
q {
	quotes: none;
}

blockquote:before,
blockquote:after,
q:before,
q:after {
	content: '';
	content: none;
}

table {
	border-collapse: collapse;
	border-spacing: 0;
}

button {
	outline: none;
	margin: 0;
	padding: 0;
}

:root {
	font-family: 'Ubuntu', sans-serif;
	--primary-color: #0A132B;
	--secondary-color: #F4f4f4;
	--tertiary-color: #C4C4C4;
	--quaternary-color: #EEF0F5;
	--danger-color: #e93b34;
	box-sizing: border-box;
	--p: 0;
	--pt: 0;
	--pr: 0;
	--pb: 0;
	--pl: 0;
	--px: 0;
	--py: 0;
	--m: 0;
	--mt: 0;
	--mr: 0;
	--mb: 0;
	--ml: 0;
	--mx: 0;
	--my: 0;
}

*,
*::before,
*::after {
	box-sizing: inherit;
}

.container {
	width: 100%;
	max-width: 1080px;
	margin: 0 auto;
}

@media (max-width: 1079px) {
	.container {
		padding: 0 20px;
	}
}


[style*='--p:'] {
	padding: calc(4px * var(--p)) !important;
}

[style*='--pt:'] {
	padding-top: calc(4px * var(--pt)) !important;
}

[style*='--pr:'] {
	padding-right: calc(4px * var(--pr)) !important;
}

[style*='--pb:'] {
	padding-bottom: calc(4px * var(--pb)) !important;
}

[style*='--pl:'] {
	padding-left: calc(4px * var(--pl)) !important;
}

[style*='--px:'] {
	padding-right: calc(4px * var(--px)) !important;
	padding-left: calc(4px * var(--px)) !important;
}

[style*='--py:'] {
	padding-top: calc(4px * var(--py)) !important;
	padding-bottom: calc(4px * var(--py)) !important;
}

[style*='--m:'] {
	margin: calc(4px * var(--m)) !important;
}

[style*='--mt:'] {
	margin-top: calc(4px * var(--mt)) !important;
}

[style*='--mr:'] {
	margin-right: calc(4px * var(--mr)) !important;
}

[style*='--mb:'] {
	margin-bottom: calc(4px * var(--mb)) !important;
}

[style*='--ml:'] {
	margin-left: calc(4px * var(--ml)) !important;
}

[style*='--mx:'] {
	margin-right: calc(4px * var(--mx)) !important;
	margin-left: calc(4px * var(--mx)) !important;
}

[style*='--my:'] {
	margin-top: calc(4px * var(--my)) !important;
	margin-bottom: calc(4px * var(--my)) !important;
}

.text--xs {
	font-size: 12px;
}

.text--sm {
	font-size: 14px;
}

.text--default {
	font-size: 16px;
}

.text--md {
	font-size: 20px;
}

.text--lg {
	font-size: 24px;
}

.fw-700 {
	font-weight: 700;
}

.text--primary {
	color: var(--primary-color);
}

.text--secondary {
	color: var(--secondary-color);
}

.text--tertiary {
	color: var(--tertiary-color);
}

.text--quaternary {
	color: var(--quaternary-color);
}

.ml-auto {
	margin-left: auto;
}

.mr-auto {
	margin-right: auto;
}

.text-center {
	text-align: center;
}

.hide {
	display: none !important;
}

.footer {
	position: sticky;
	bottom: 0;
	width: 100%;
	padding: 16px 0;
	background-color: white;
}